import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController, AlertController, ModalController, Searchbar, ToastController,  } from 'ionic-angular';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { SoapService } from '../soap.service';

import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_code } from '../../config';
/**
 * Generated class for the AddAdminDirektoratPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-add-admin-direktorat',
  providers: [SoapService],
  templateUrl: 'add-admin-direktorat.html',
}) 
export class AddAdminDirektoratPage {
  //public addAdminDirektoratForm: FormGroup;
  nippList:any = [];
  nippSearchResultList:Array<any> = [];
  nippSelected:any;
  index:any;
  showResult:boolean= false;
  nippInputFocused:any;
  direktoratList: Array<any>=[];
  isLoading:boolean = false;
  errorMessage:any='';
  direktorat:any='';
  currentDir:any='';
  namaDirektorat:any='';
  codeDirektorat:any='';

  loading:any;

  editData:any = '';

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private _fb: FormBuilder,
    public viewCtrl: ViewController,
    public soapService:SoapService,
    public loadingCtrl:LoadingController,
    public storage:Storage,
    public alertCtrl:AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController,
    
  ) {
    this.editData = navParams.get('editData');
    //console.log(this.editData); 
    if ( this.editDataAda(this.editData)) { 
      this.currentDir = this.editData['DIREKTORAT'];
      this.nippList.push({value:this.editData['NIPP']});
    } else {
      this.nippList.push({value : ''});
    }
    this.getDataDirektorat();

    // this.addAdminDirektoratForm = this._fb.group({
    //   fcDirektorat: ['', Validators.compose([
    //     Validators.required
    //   ])],
    //   fcAdmin: ['', Validators.compose([
    //     Validators.required
    //   ])],           
    // });


  }

  result(){
    //console.log('this.anArray',this.nippList);   
    }

  addNIPP() { 
    //console.log('clicked');  
    this.nippList.push({value:''});
  }

  removeNIPP(i) {  
    this.nippList.splice(this.index, 1);
  }

  setNIPP(nippSearchResult,i) {

    this.index = i;
    this.nippList[this.index]['value'] = nippSearchResult['NIPP'];
    this.showResult=false;
    //console.log(this.nippList[this.index]);
  }

  checkFocus(i) {
    this.nippInputFocused = i;
    //console.log(this.nippInputFocused);
  }

  getNIPP(index, nipp) {
    //console.log(this.nippList[index]['value']);
    this.soapService
    .post(api_base_url,'eoffice_find_nipp_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass, 
        search      : this.nippList[index]['value']
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.nippSearchResultList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.nippSearchResultList.push(responData['data'][i]);
          }                        
        }
        this.showResult = true;         
        //console.log(this.nippSearchResultList);
        
      } else {
        this.nippSearchResultList = []; 
        // let alert = this.alertCtrl.create({
        //   title: '',
        //   subTitle: 'Gagal mendapatkan pesan (1)',
        //   buttons: ['OK']
        // });
        // alert.present();
      }      
    })
    .catch(error => {
      console.log(error);
      // let alert = this.alertCtrl.create({
      //   title: '',
      //   subTitle: 'Gagal mendapatkan pesan (2)',
      //   buttons: ['OK']
      // });
      // alert.present();    
      
    });
  } 

  getDataDirektorat() {
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    loading.present();
    this.isLoading = true;
    this.soapService
    .post(api_base_url,'eoffice_data_direktorat_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass            
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      // console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.direktoratList = []; 
        if (responData['data'].length > 0) {
          for (let i=0; i < responData['data'].length; i++) {
            this.direktoratList.push(responData['data'][i]);
          }                        
        }            
        //console.log(this.direktoratList);       
      } else {        
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present(); 
      }  
      loading.dismiss();
      this.isLoading = false;    
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();  

      loading.dismiss();
      this.isLoading = false;       
      
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AddAdminDirektoratPage');
  }

  register() {
    //console.log('send data');
    //console.log(this.nippList); 
    let nippData = [];
    for (let i = 0; i < this.nippList.length;i++) {
      if (this.nippList[i]['value'] != '') {
        nippData.push(this.nippList[i]['value']);
      }
    }

    if (this.direktorat == 'create') {
      if (nippData.length > 0 && this.direktorat != '' && this.codeDirektorat != '' && this.namaDirektorat != '' ) {
        this.AddDirektorat(nippData);
        //this.sendData(nippData, dir);
      } else {
       this.errorMessage = '*Semua field harus diisi';
      }
    } else {
      if (nippData.length > 0 && this.direktorat != '' ) {

        this.sendData(nippData,this.direktorat);
      } else {
       this.errorMessage = '*Semua field harus diisi';
      }
    }
    
    //console.log(nippData);
  }

  sendData(nippData, dir) {
    let loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });

    if (this.direktorat != 'create') {
      loading.present();
      this.isLoading = true;
    } else {
      this.loading.dismiss();
    }
    
    this.soapService
    .post(api_base_url,'eoffice_regis_user_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,
        nipp: nippData,
        id_dir: dir,
        id_level: '1',
        user_choice: '0',
        id_kategori: ''      

      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        let toast = this.toastCtrl.create({
          message: 'Register berhasil',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
        this.navCtrl.pop();
      } else {
        this.nippSearchResultList = []; 
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present(); 
      }  

      if (this.direktorat != 'create') {
        loading.dismiss();      
      } else {
        this.loading.dismiss();
      }
      
    })
    .catch(error => {
      //console.log(error);
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.', 
        duration: 3000,
        position: 'bottom'
      });
      toast.present();  

      if (this.direktorat != 'create') {
        loading.dismiss();      
      } else {
        this.loading.dismiss();
      }
    });
  }

  update() {
    // console.log( {            
    //   usernameEDI : api_user, 
    //   passwordEDI : api_pass,
    //   nippnew : this.nippList[0]['value'],
    //   nippold : this.editData['NIPP'],
    //   id_level : '1'      
    // });
    if (this.nippList[0]['value'] == '' ) {
      this.errorMessage = '*Semua field harus diisi';
    } else {
      let loading = this.loadingCtrl.create({
        spinner: 'dots',
        content: "Mohon Tunggu...",       
        cssClass: 'transparent',
        //dismissOnPageChange:true
      });
      loading.present();
      this.isLoading = true;
      this.soapService
      .post(api_base_url,'eoffice_upd_user_ipcc',{fStream:JSON.stringify(
        {            
          usernameEDI : api_user, 
          passwordEDI : api_pass,
          nippnew : this.nippList[0]['value'],
          nippold : this.editData['NIPP'],
          id_level : '1'      
        }
      )}).then(result => {
        var responData = JSON.parse(String(result));
        //console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          let toast = this.toastCtrl.create({
            message: 'Update Berhasil',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
          this.navCtrl.pop();
        } else {
          this.nippSearchResultList = []; 
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present(); 
        }  
        loading.dismiss();      
      })
      .catch(error => {
        //console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();  
  
        loading.dismiss();             
      });
    }
    
  }

  AddDirektorat(nippData) {
    this.loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    this.loading.present();
    this.isLoading = true;
    this.soapService
    .post(api_base_url,'eoffice_add_dir_ipcc',{fStream:JSON.stringify(
      {
        usernameEDI : api_user, 
        passwordEDI : api_pass,       
        nama_dir : this.namaDirektorat,
        kode_dir : this.codeDirektorat        
      }
    )}).then(result => {
      var responData = JSON.parse(String(result));
      //console.log(responData);
      if (responData['rcmsg'] == "SUCCESS") {
        this.sendData(nippData, responData['data']['ID_DIR']);
      } else {
        let toast = this.toastCtrl.create({
          message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();
      }        
    })
    .catch(error => {
      //console.log(error);      
      let toast = this.toastCtrl.create({
        message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
        duration: 3000,
        position: 'bottom'
      });
      toast.present();              
    });
  }
 
  editDataAda(val) {
    if (typeof val != "undefined") {
     // console.log("true"); 
      return true;
    } else {
      //console.log("false");
      return false; 
    }
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

  loadingPresent() {
    this.loading = this.loadingCtrl.create({
      spinner: 'dots',
      content: "Mohon Tunggu...",       
      cssClass: 'transparent',
      //dismissOnPageChange:true
    });
    this.loading.present();
  }

  loadingDismiss() {
    if(this.loading.present()) {
      this.loading.dismiss(); 
    }
  }

}



